      register() {
        let data = {
          userPhone: this.mobile_num,
          phoneCode: this.code
        };
        requestLogin('external/webLoginMsg/v2', data).then(res => {
          if (res.code == '0') {
            if(this.isYfbd()) {
              window._agl && window._agl.push(['track', ['success', {t: 3}]]);
            }
            localStorage.sessionId = res.data.sessionId;
            localStorage.userId = res.data.userId;
            localStorage.realPackageName = res.data.realPackageName;
            sa.login(res.data.userPhone)
            sa.quick('autoTrack', {
              PlatformType: 'H5',
              appname: 'xyH5_SEM'
            })
            this.test();
          } else {
            this.$message({
              text: res.msg,
              type: 'warn'
            })
          }
        })
