          userIdNumber: this.idcard,
          userPhone: this.mobile_num,
          // proCode: 'rejectSimple', // 
          proCode: 'rejectComplete', // 
          randomId: localStorage.getItem('randomId')
        }
        api.request('order/checkoutCounterV4', JSON.stringify(obj), 'application/json; charset=utf-8').then(res => {
          if (res.code == 0) {
            this.orderInfo.orderCode = res.data.orderCode
            this.orderInfo.orderId = res.data.orderId
            this.orderInfo.orderRealAmount = res.data.orderRealAmount
            if (res.data.orderStatus == 40) {
              this.$router.push({
                name: 'detail4',
                query: {
                  reportId: res.data.reportId,
                  isSample: 0
                }
              })
              return
            } else if (res.data.orderStatus == 11 || res.data.orderStatus == 20 || res.data.orderStatus == 30) {
              this.$router.push({
                name: 'testing3'
              })
              return
            } else if (res.data.orderStatus = 10) {
              if (localStorage.latestOrder) {
                if (JSON.parse(localStorage.latestOrder).orderId != res.data.orderId) {
                  // sa.track('B4YJ_MakeOrder', {productName: 'blacklist'})
                }
              } else {
                // sa.track('B4YJ_MakeOrder', {productName: 'blacklist'})
              }
              this.orderInfo = res.data;
              localStorage.orderId = this.orderInfo.orderId
              localStorage.orderCode = this.orderInfo.orderCode
              localStorage.latestOrder = JSON.stringify(res.data)
              localStorage.RealAmount = res.data.orderRealAmount;
              localStorage.testData = JSON.stringify(obj);
              this.isPay = true
              this.slideShow = true
            }
          } else {
            this.$message({
              text: res.msg,
              type: 'warn'
            })
          }
        }).catch(err => {
          this.$message({
            text: ',',
            type: 'error'
          })
        })
