    transition-delay:3s;
    opacity: 0;
  }

  .slide-fade4-enter-active {
    transition: all 1.3s ease;
    transition-delay:4s;
  }
  .slide-fade4-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
