  }
  .slide-fade5-enter
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(0px);
    transition-delay:5s;
    opacity: 0;
  }


  ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    font-family: PingFangSC-Regular;
    font-size: 0.14rem;
