<template>
	<!--pages/agreement/agreement.wxml-->
<div class='container text-box'>
  <div><span class='qa-title'>{{title}}</span></div>
  <div v-for="item in qaList" :key="item.q">
    <div class="qa-pannel">
      <div>
        <span class='qa-q'>{{item.q}}</span>
      </div>
      <div>
        <span class='qa-a'>{{item.a}}</span>
      </div>
      <div>
        <span class='qa-tips'>{{item.tips}}</span>
      </div>
    </div>
  </div>
</div>
</template>

<script>
	export default {
		name: 'agreement',
		data() {
			return {
				title:"",
			    qaList:[{
			      q:"",
			      a:"",
			      tips:"123"
			    }, 
			    {
			      q: "",
			      a: "",
			      tips: "123"
			    }, 
			    {
			      q: "",
			      a: "",
			      tips: "123"
			    }, 
			    {
			      q: "",
			      a: "4\n1.	\n2.	\n3.	\n4.	",
			      tips: ""
			    }]
			}
		}
	}
</script>

<style>
	/* pages/agreement/agreement.wxss */
/* pages/qa/qa.wxss */
.text-box {
  height: auto;
  padding: 0.12rem;
}
.qa-title{
  width: auto;
  display: inline-block;
  font-size: 0.16rem;
  padding:0.1rem;
  border-bottom:1px solid #F6871F;
  margin-bottom: 0.2rem;
}
.qa-q, .qa-a, .qa-tips{
  font-size: 0.13rem;
}
.qa-q{
  font-weight:bold;
}
.qa-tips{
  color:#F6871F;
}
.qa-pannel{
  padding-bottom: 0.15rem;
}
</style>


